.chatWindow{
    display: flex;
    flex-direction: column;
    height: 100%;
}
.chatWindow-header{
    height: 60px;
    border-bottom: 1px solid #CCC;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.chatWindow-headerInfo{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.chatWindow-avatar{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-left: 15px;
    margin-right: 15px;
}
.chatWindow-name{
    font-size: 17px;
    color: #000;
}
.chatWindow-headerButtons{
    display: flex;
    align-items: center;
    margin-right: 15px;

}
.chatWindow-btn{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    overflow: hidden;
    transition: all ease 0.3s;
}
.chatWindow-body{   
    padding: 20px 30px;
    flex: 1;
    overflow-y: auto;
    background-color: #E5DDD5;
    background-size: cover;
    background-position: center;
    background-image: url('https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png');
}
.chatWindow-body::-webkit-scrollbar{
    width: 6px;
    height: 6px;
}
.chatWindow-body::-webkit-scrollbar-thumb{
    background-color: rgba(0,0,0,0.2);
}
.chatWindow-footer{
    height: 62px;
    display: flex;
    align-items: center;
}
.chatWindow-pre{
    display: flex;
    margin: 0 15px;
}
.chatWindow-pos{
    display: flex;
    margin: 0 15px;
}
.chatWindow-inputArea{
    flex: 1;
}
.chatWindow-input{
    width: 100%;
    height: 40px;
    border: 0;
    outline: 0;
    background-color: #fff;
    border-radius: 20px;
    font-size: 15px;
    color: #4A4A4A;
    padding-left: 15px;
}
.chatWindow-emojiArea{
    height: 200px;
    overflow-y: hidden;
    transition: all ease 0.3s;
}
aside.emoji-picker-react{
    background: none;
}